import { makeStyles } from "@material-ui/core/styles"
import { graphql, useStaticQuery } from "gatsby"
import Img from "gatsby-image"
import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import WhyUs from "../components/whyUs"

const useStyles = makeStyles(() => ({
  image: {},
  contentContainer: {
    position: "relative",
    textAlign: "center",
    color: "white",
    // minHeight: "calc(100vh - 16vh)",
  },
  imageText: {
    position: "absolute",
    top: "50%",
    left: "50%",
    paddingBottom: "3vh",
    transform: "translate(-50%, -50%)",
  }
}))

const IndexPage = () => {
  const classes = useStyles()
  const query = useStaticQuery(graphql`
    query {
      allImageSharp(filter: {fluid: {originalName: {eq: "old_steel.webp"}}}) {
        edges {
          node {
            id
            fluid {
              ...GatsbyImageSharpFluid
              originalName
            }
          }
        }
      }
    }
  `)

  return (
    <Layout>
      <SEO title="Home" />
      <div className={classes.contentContainer}>
        {query.allImageSharp.edges.map(edge => {
            return (
              <div>
                <Img style={{maxHeight: 500}} fluid={edge.node.fluid} />
              </div>
            )
          })}
        <div className={classes.imageText}>
          <h1 style={{fontSize: "5.9vw"}}>GIVE YOUR OLD METAL A NEW LOOK</h1>
          <span style={{fontSize: "2.5vw"}}>Reliable sandblasting and powder coating professionals serving the greater Toronto area</span>
        </div>
        <div style={{margin: "1em"}}>
          <span style={{fontSize: "xx-large", color: "#0F93FE"}}>Call Now 519 658 7069</span>
        </div>
      </div>
      <WhyUs />
    </Layout>
  )
}

export default IndexPage
